.social {
  display: flex;
  justify-content: space-around;
  background-color: #d3d3d3;
  padding-top: 54px;
  padding-bottom: 40px;
  align-items: center;
  overflow: hidden;
}
@media (max-width: 918px) {
  .face {
    display: none;
  }
  .google {
    display: none;
  }
}
@media (max-width: 750px) {
  .stripe {
    display: none;
  }
  .box {
    display: none;
  }
}
@media (max-width: 480px) {
  .apple {
    display: none;
  }
  .sega {
    display: none;
  }
}
