.tips {
  padding-top: 90px;
  padding-bottom: 87px;
}

.tip-box {
  display: flex;
  justify-content: center;
}
.tips p {
  font-family: Roboto, sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
  text-align: left;
  color: #333333;
  padding-left: 144px;
}
.tip h3,
h4 {
  padding-left: 40px;
}
.tip h3 {
  padding-bottom: 13px;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: #333333;
}
.tip h4 {
  font-family: Roboto, sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: #333333;
}

@media (max-width: 1350px) {
  .tip-box {
    display: grid;
  }
  .tips p {
    padding-left: 0;
    text-align: center;
    padding-bottom: 30px;
  }
  .tip {
    padding-bottom: 40px;
  }
  .tip h3 {
    text-align: center;
    padding-left: 0;
  }
  .tip h4 {
    text-align: center;
    padding-left: 0;
  }
  .tip img {
    display: flex;
    margin: 0 auto;
    width: 300px;
  }
}
