.header {
  background-color: #6b6b6b;
  display: flex;
  justify-content: space-around;
  padding-top: 35px;
  padding-bottom: 35px;
}
.header ul {
  display: flex;
  gap: 40px;
}
.header ul li a {
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  color: #fff;
}
.header ul li a:hover {
  color: #dacece;
  transition: 0.4s;
}
@media (max-width: 500px) {
  .header ul {
    gap: 20px;
  }
}
.footer-last {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #111111;
}
.footer-last p {
  padding-left: 144px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  color: #777777;
}
@media (max-width: 870px) {
  .footer-last p {
    text-align: center;
    padding-left: 0;
  }
}
