.text {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #ebebeb;
  padding-bottom: 74px;
  padding-top: 67px;
}
.text-text h1 {
  padding-bottom: 11px;
  font-family: Roboto, sans-serif;
  font-size: 39px;
  font-weight: 400;
  line-height: 45px;
  text-align: left;
  color: #333333;
}
.text-text p {
  padding-bottom: 30px;
  font-family: Roboto, sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 26px;
  text-align: left;
  color: #333333;
}
.text-text button {
  padding: 15px 25px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  border-radius: 5px;
  background-color: #fff;
  color: #333333;
  border: none;
}
.text-text button:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
  transition: all 0.6s ease;
  cursor: pointer;
}
.text-img p {
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  color: #00000080;
}
.br {
  display: none;
}
@media (max-width: 882px) {
  .text {
    display: grid;
  }
  .text-text h1 {
    text-align: center;
  }
  .text-text p {
    text-align: center;
  }
  .text-text button {
    display: flex;
    margin: 0 auto;
  }
  .text-img img {
    display: flex;
    margin: 0 auto;
  }
}
@media (max-width: 574px) {
  .br {
    display: block;
  }
  .text-text h1 {
    font-size: 26px;
    line-height: 32px;
  }
  .text-text p {
    font-size: 14px;
    line-height: 24px;
  }
  .text-img img {
    width: 300px;
  }

  .text-text button {
    padding: 15px 7px;
  }
}
